<template>
    <section id='footerCtrl'>
        <section @click = "gotoAddress('/oces/home')" class="guide_item">
            <i class="iconfont" :class="[chooseMenuOne === true ? 'icon-iconfont-shouye2' : 'icon-iconfont-shouye']"></i>
            <div :class="[chooseMenuOne === true ? 'font-title-choose' : 'footer-title']">首页</div>
        </section>
        <section @click = "gotoAddress('/oces/courses')" class="guide_item" style="width: 34%">
            <i class="iconfont" :class="[chooseMenuTwo === true ? 'icon-iconfont-kecheng1' : 'icon-iconfont-kecheng']"></i>
            <div :class="[chooseMenuTwo === true ? 'font-title-choose' : 'footer-title']">课程</div>
        </section>
        <section @click = "gotoAddress('/oces/my')" class="guide_item">
            <i class="iconfont" :class="[chooseMenuThree === true ? 'icon-iconfont-wo' : 'icon-iconfont-wo1']"></i>
            <div :class="[chooseMenuThree === true ? 'font-title-choose' : 'footer-title']">我</div>
        </section>
    </section>
</template>

<script>
    export default {
      data () {
        return {
          chooseMenuOne: true,
          chooseMenuTwo: false,
          chooseMenuThree: false
        }
      },
      created () {
      },
      mounted () {
        switch (this.$route.path) {
          case '/oces/home':
            this.chooseMenuOne = true
            this.chooseMenuTwo = false
            this.chooseMenuThree = false
            break
          case '/oces/courses':
            this.chooseMenuOne = false
            this.chooseMenuTwo = true
            this.chooseMenuThree = false
            break
          case '/oces/my':
            this.chooseMenuOne = false
            this.chooseMenuTwo = false
            this.chooseMenuThree = true
            break
        }
      },
      computed: {
      },
      methods: {
        gotoAddress (path) {
          this.$router.push(path)
          switch (path) {
            case '/oces/home':
              this.chooseMenuOne = true
              this.chooseMenuTwo = false
              this.chooseMenuThree = false
              break
            case '/oces/courses':
              this.chooseMenuOne = false
              this.chooseMenuTwo = true
              this.chooseMenuThree = false
              break
            case '/oces/my':
              this.chooseMenuOne = false
              this.chooseMenuTwo = false
              this.chooseMenuThree = true
              break
          }
        }
      },
      watch: {
        $route () {
          switch (this.$route.path) {
            case '/oces/home':
              this.chooseMenuOne = true
              this.chooseMenuTwo = false
              this.chooseMenuThree = false
              break
            case '/oces/courses':
              this.chooseMenuOne = false
              this.chooseMenuTwo = true
              this.chooseMenuThree = false
              break
            case '/oces/my':
              this.chooseMenuOne = false
              this.chooseMenuTwo = false
              this.chooseMenuThree = true
              break
          }
        }
      }
    }

</script>
<style lang="scss" scoped>
  @import '../../style/mixin';
  #footerCtrl{
    background-color: #fff;
    position: fixed;
    z-index: 100;
    left: 0;
    right: 0;
    bottom: 0;
  @include wh(100%, 2.0906rem);
    display: block;
    width: 100%;
    white-space: nowrap;
    box-sizing: border-box;
    box-shadow: 0 -0.026667rem 0.053333rem rgba(0,0,0,.1);
  }
  .guide_item{
    /*flex: 1;*/
    text-align: center;
    width: 33%;
    display: inline-block;
    float: left;
    box-sizing: border-box;
    height: 100%;
  .icon_style{
  @include wh(.8rem, .8rem);
    margin-bottom: .2rem;
    margin-top: .3rem;
    fill: #ccc;
  }
  }
  .iconfont {
    font-family:"iconfont" !important;
    font-size: 1.25rem;
    font-style:normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .icon-iconfont-shouye:before {
    content: "\e624";
    color: #999999;
  }
  .icon-iconfont-shouye2:before {
    content: "\e626";
    color: #24b4d5;
  }
  .icon-iconfont-wo:before {
    content: "\e627";
    color: #24b4d5;
  }
  .icon-iconfont-wo1:before {
    content: "\e628";
    color: #999999;
  }
  .icon-iconfont-kecheng:before {
    content: "\e629";
    color: #999999;
  }
  .icon-iconfont-kecheng1:before {
    content: "\e62a";
    color: #24b4d5;
  }
  .footer-title{
  @include fs(20px,#999999)
  }
  .font-title-choose{
  @include fs(20px,#24b4d5)
  }
</style>



